* {
  box-sizing: border-box;
  margin: 0;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

.card {
  padding-inline: 16px;
  border: 1px solid #f3f3f3;
  flex-direction: column;
  width: 200px;
  padding-top: 32px;
  padding-bottom: 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.card__name-product {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
